/**
 * 
 * URL
 */
function urlEncode(data) {
    var _result = [];
    for (var key in data){
        var value = data[key];
        if (value.constructor === Array){
            console.log('get');
        }else{
            _result.push(key + '=' + value);
        }
    }
    return _result.join('&');
}



/**
 * @author William Cui
 * @description URL
 * @param name {string} 
 * @returns value {string} 
 **/
function getQueryString(name) {
    let reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
    let r = window.location.search.substr(1).match(reg);
    if (r !== null) return unescape(r[2]);
    return null;
}

/**
 * @author William Cui
 * @description 
 * @param number {number} 
 * @param n {number} 
 * @returns {string} 
 **/
function fillZero(number, n) {
    return (Array(n).join(0) + number).slice(-n);
}

/**
* @author William Cui
* @description 
* @param timestamp {number} 
* @param patternStr {string}  'YYYY-MM-DD hh:mm:ss'
* @returns {string} 
Y:  M:  D:  h:  m: , s: 
**/
function stampToDate(timestamp, patternStr) {
    patternStr = patternStr || 'YYYY-MM-DD hh:mm:ss';
    const patternArray = patternStr.match(/\w+/g);
    const date = new Date(timestamp);
    const dateObj = {
        Y: date.getFullYear(),
        M: date.getMonth() + 1,
        D: date.getDate(),
        h: date.getHours(),
        m: date.getMinutes(),
        s: date.getSeconds()
    };
    patternArray.forEach(pattern => {
        let replaceStr = fillZero(dateObj[pattern[0]], pattern.length);
        patternStr = patternStr.replace(pattern, replaceStr);
    });
    return patternStr;
}

/**
 * @author William Cui
 * @description 
 * @param dateStr {string} 
 * @returns {number} 
 **/
function dateToStamp(dateStr) {
    return new Date(dateStr).getTime();
}

export { getQueryString, stampToDate, dateToStamp,urlEncode };



